h1 {
  text-align: center;
}
#editor {
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
  width: 100%;
  padding: .5rem;
  border-radius: .5rem;
  border: 1px solid orangered;
  outline: none;
  resize: vertical;
  min-height: 10em;
}
#preview {
  background-color: rgba(131, 139, 143, 0.534);
  padding: 1rem;
  border-radius: .5rem;
}
#preview h1 {
  text-align: left;
}

#preview p > code, pre {
  display: block;
  background-color: rgba(109, 181, 214, 0.534);
  border-radius: .5rem;
  padding: .5rem;
}
#preview a {
  display: block;
  max-width: max-content;
  text-decoration: none;
  color: orangered;
  font-size: 1.1rem;
  font-weight: bold;
}
#preview a::before, a::after {
  content: "**"
}
#preview a:hover {
  color: orange;
}
#preview img {
  max-width: 3em;
}